import { Component, Input, OnChanges } from '@angular/core';
import { BaseComponent } from '@kenedy-ui/constants/base.component';
import { Orientation, Position, RadioInterface } from './types/radio.type';
import { Color } from '../button/types/button.type';

@Component({
  selector: 'knd-radio',
  templateUrl: './radio.component.html',
})
export class RadioComponent extends BaseComponent implements OnChanges {
  roundedChildSize = 0;
  @Input() size: number = 16;
  @Input() color: Color = 'ped';
  @Input() orientation: Orientation = 'horizontal';
  @Input() data: RadioInterface[] = [];

  handleClick(value: unknown) {
    this.value = value;
    this.onclick.emit(value);
  }

  get border(): string {
    return `base-border-${this.color}`;
  }

  get background(): string {
    return `base-solid-${this.color}`;
  }

  orientationPosition(position: Position = 'right'): string {
    const pos = {
      top: 'flex-col-reverse',
      bottom: 'flex-col',
      left: 'flex-row-reverse',
      right: 'flex-row',
    };
    let result = this.orientation === 'horizontal' ? 'mx-1' : 'my-1';
    result = `${result} ${pos[position]}`;
    return result;
  }

  ngOnChanges(): void {
    this.roundedChildSize = this.size - 9;
  }
}
