import { Component, Input, OnChanges } from '@angular/core';
import { BaseComponent } from '@kenedy-ui/constants/base.component';
import {
  TextFieldErrorInterface,
  TextFieldType,
  TextFieldVariant,
} from './types/text-field.type';
import { FormGroup } from '@angular/forms';

@Component({
  selector: 'knd-text-field',
  templateUrl: './text-field.component.html',
})
export class TextFieldComponent extends BaseComponent implements OnChanges {
  ngOutlined = '';

  @Input() label!: string;
  @Input() type: TextFieldType = 'text';
  @Input() formGroup!: FormGroup;
  @Input() placeholder!: string;
  @Input() errors: TextFieldErrorInterface[] = [];
  @Input() variant: TextFieldVariant = 'default';

  ngOnChanges(): void {
    this.classes = this.classes?.split('_')?.[0];
    this.ngOutlined = '';
    let addClass = 'border-t-0 border-l-0 border-r-0';
    switch (this.variant) {
      case 'outlined':
        this.ngOutlined =
          'absolute -top-[7px] left-[9px] bg-white cursor-default';
        break;
      case 'filled':
        this.classes = `_ ${addClass} rounded-b-none rounded-t-md rounded-r-none rounded-l-none bg-gray-100`;
        break;
      case 'standard':
        this.classes = `_ ${addClass} !rounded-none`;
        break;
    }
  }
}
