import { Component, Input, OnChanges } from '@angular/core';
import { BaseComponent } from '@kenedy-ui/constants/base.component';
import { ButtonType, ButtonVarint, Color } from './types/button.type';

@Component({
  selector: 'knd-button',
  templateUrl: './button.component.html',
})
export class ButtonComponent extends BaseComponent implements OnChanges {
  @Input() type: ButtonType = 'button';
  @Input() text!: string;
  @Input() variant: ButtonVarint = 'solid';
  @Input() color: Color = 'ped';

  constructor() {
    super();
  }

  handleClick() {
    this.onclick.emit(this.value);
  }

  ngOnChanges(): void {
    this.classes = this.classes?.split('_')?.[0];
    if (this.variant === 'solid' && this.type !== 'disabled') {
      this.classes = `${this.classes} _ base-${this.variant}-${this.color} ${
        this.color === 'light' ? 'text-black' : 'text-white'
      }`;
    }
    if (this.type === 'disabled') {
      this.classes = `${this.classes} _ base-color-disable base-background-disable cursor-no-drop`;
    }
    if (this.variant !== 'solid' && this.type !== 'disabled') {
      this.classes = `${this.classes} _ base-${this.variant}-${this.color} ${
        this.color === 'light' ? 'text-black' : `base-color-${this.color}`
      }`;
    }
    this.classes = this.classes.replaceAll('undefined', '');
  }
}
