import {
  Component,
  EventEmitter,
  Input,
  OnChanges,
  Output,
} from '@angular/core';
import { BaseComponent } from '@kenedy-ui/constants/base.component';
import { IconName } from '../icon/types/icon.type';
import { Color } from '../button/types/button.type';
import { Position } from '../radio/types/radio.type';

@Component({
  selector: 'knd-checkbox',
  templateUrl: './checkbox.component.html',
})
export class CheckboxComponent extends BaseComponent implements OnChanges {
  iconSize = 0;
  classPosition = '';
  @Input() size: number = 20;
  @Input() iconName: IconName = 'check';
  @Input() color: Color = 'ped';
  @Input() label!: string;
  @Input() position: Position = 'right';

  @Input() override name!: string;
  @Input() override classes!: string;
  @Input() override value!: unknown;
  @Output() override onclick = new EventEmitter();

  get retriveClass(): string {
    if (this.value) {
      return `base-solid-${this.color}`;
    }
    return 'bg-transparent';
  }

  handleClick() {
    this.value = this.name === this.value ? null : this.name;
    this.onclick.emit({
      name: this.name,
      label: this.label,
      value: this.value,
    });
  }

  ngOnChanges() {
    this.iconSize = this.size - 9;
    this.classPosition =
      this.position === 'right'
        ? 'flex items-center'
        : this.position === 'left'
        ? 'flex flex-row-reverse items-center'
        : this.position === 'top'
        ? 'flex flex-col-reverse'
        : 'flex flex-col';
  }
}
