import type { OnInit } from '@angular/core';
import { Component, Input } from '@angular/core';
import { BaseComponent } from '@kenedy-ui/constants/base.component';
import type { IconName } from './types/icon.type';

@Component({
  selector: 'knd-icon',
  templateUrl: './icon.component.html',
})
export class IconComponent extends BaseComponent implements OnInit {
  @Input() override name: IconName = 'house';
  @Input() type: 'fas fa-' | 'fa fa-' = 'fa fa-';

  handleClick() {
    this.onclick.emit(this.value);
  }

  ngOnInit(): void {
    this.classes = `${this.type}${this.name} ${this.classes}`.replace(
      'undefined',
      ''
    );
  }
}
